<template>
  <div class="login-container">
    <el-card class="login-card">
      <h2 class="login-title">oci-helper Login</h2>
      <el-form @submit.prevent="login" @keydown.enter="login">
        <el-form-item label="账号" label-width="20%">
          <el-input v-model="loginParams.account" placeholder="请输入账号"/>
        </el-form-item>
        <el-form-item label="密码" label-width="20%">
          <el-input show-password v-model="loginParams.password" placeholder="请输入密码" type="password"/>
        </el-form-item>
        <el-form-item style="text-align: center;">
          <div class="login-button-container">
            <el-button type="primary" @click="login" :loading="loading" class="login-button">Login</el-button>
          </div>
        </el-form-item>
      </el-form>
    </el-card>
    <!-- Footer 放在登录框下面 -->
    <el-footer class="login-footer">
      <el-link href="https://github.com/Yohann0617" target="_blank">© Yohann All Rights Reserved</el-link>
    </el-footer>
  </div>
</template>

<script>
import { ElMessage } from "element-plus";

export default {
  data() {
    return {
      loginParams: {
        account: '',
        password: ''
      },
      loading: false
    };
  },
  methods: {
    async login() {
      this.loading = true;
      try {
        const response = await this.$axios.post('/oci/login', { ...this.loginParams });
        if (response.data.success) {
          ElMessage({
            message: response.data.msg,
            type: 'success',
            duration: 2000 // 设置提示显示时间，单位是毫秒
          });
          sessionStorage.setItem('token', response.data.data);
          setTimeout(() => {
            this.$router.push('/dashboard');
          }, 2000);
        } else {
          ElMessage({
            message: response.data.msg,
            type: 'error',
            duration: 2000 // 设置提示显示时间，单位是毫秒
          });
          this.loading = false;
        }
      } catch (error) {
        console.error('Error:', error);
        ElMessage({
          message: '登录失败，请重试',
          type: 'error',
          duration: 2000
        });
        this.loading = false;
      }
    }
  }
};
</script>

<style scoped>
.login-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: #f0f2f5;
}

.login-card {
  width: 400px;
  padding: 20px;
  margin-bottom: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.login-title {
  text-align: center;
  margin-bottom: 20px;
  color: #304156;
}

.login-button-container {
  text-align: center;
  margin-left: 10%;
}

.login-button {
  width: 300px; /* 设置按钮宽度 */
}

.login-footer {
  text-align: center;
  font-size: 14px;
  margin-top: 20px;
  color: #304156;
}
</style>
