<template>
  <el-button @click="goBack">返回配置列表</el-button>
  <el-button @click="getOciCfgDetails" :loading="flushLoading">刷新页面</el-button>
  <span style="margin-left: 20px;"><el-text type="primary">配置：{{ this.user.username }} —— 配置详情</el-text></span>
  <el-divider class="divider"/>
  <div class="demo-collapse">
    <el-collapse accordion>
      <el-collapse-item name="1">
        <template #title>
          <el-icon class="header-icon">
            <info-filled/>
          </el-icon>
          API配置详情（点击向下⬇展开）
        </template>
        <div><span style="margin-left: 20px;"><el-text type="info">region={{ this.ociCfgDetails.region }}</el-text></span></div>
        <div><span style="margin-left: 20px;"><el-text type="info">user={{ this.ociCfgDetails.userId }}</el-text></span></div>
        <div><span style="margin-left: 20px;"><el-text type="info">tenancy={{ this.ociCfgDetails.tenantId }}</el-text></span></div>
        <div><span style="margin-left: 20px;"><el-text type="info">fingerprint={{ this.ociCfgDetails.fingerprint }}</el-text></span></div>
        <div><span style="margin-left: 20px;"><el-text type="info">key_file={{ this.ociCfgDetails.privateKeyPath }}</el-text></span></div>
      </el-collapse-item>
    </el-collapse>
  </div>
  <div class="instance-list">
    <div v-for="instance in ociCfgDetails.instanceList" :key="instance.ocId" class="instance-card">
      <div class="header">
        <el-icon class="server-icon">
          <cpu/>
        </el-icon>
        <h3>{{ instance.name }}</h3>
      </div>
      <el-divider class="divider"/>
      <p><strong>Region</strong>
        <el-tag style="margin-left: 5%">{{ instance.region }}</el-tag>
      </p>
      <p><strong>Shape</strong>
        <el-tag :type="instance.shape.indexOf('A1') !== -1 ? 'success' : 'info'" style="margin-left: 5%">
          {{ instance.shape }}
        </el-tag>
      </p>
      <div class="ip-list" style="display: flex; align-items: center;">
        <p style="margin: 0; margin-right: 10px; font-weight: bold;">公网IP</p>
        <div v-for="(ip, ipIndex) in instance.publicIp" :key="ipIndex" class="ip-item" style="margin-right: 10px;">
          <el-tag effect="plain"> {{ ip }}</el-tag>
        </div>
      </div>
      <p><strong>配置</strong>
        <el-tag style="margin-left: 5%" effect="plain" type="info">
          {{ instance.ocpus }} 核 / {{instance.memory}} GB / {{instance.bootVolumeSize}} GB
        </el-tag>
      </p>
      <p><strong>创建时间</strong>
        <el-tag style="margin-left: 5%" effect="plain" type="info" >
          {{ instance.createTime }}
        </el-tag>
      </p>
      <el-button @click="handleChangeIp(instance.ocId)" style="margin-top: 5%; margin-left: -2%" type="primary"> 更换IP
      </el-button>
      <el-button @click="handleStopChangeIp(instance.ocId)" v-if="instance.enableChangeIp === 1" style="margin-top: 5%"
                 type="danger"> 停止更换IP
      </el-button>
    </div>
  </div>
  <el-dialog v-model="dialogVisible" title="可指定多个 CIDR 网段，回车保存，传空则随机更换一个IP" width="40%" @close="dialogVisible = false">
    <el-tag v-for="tag in changeIpParams.cidrList" :key="tag" closable @close="handleClose(tag)"> {{ tag }}</el-tag>
    <el-input v-if="inputVisible" ref="InputRef" v-model="inputValue" class="w-20" @keyup.enter="handleInputConfirm"
              @blur="handleInputConfirm"/>
    <el-button v-else class="button-new-tag" size="small" @click="showInput"> + New CIDR</el-button>
    <template #footer>
      <el-button @click="dialogVisible = false">取消</el-button>
      <el-button type="primary" @click="changeIp" :loading="changeIpLoading">新增更换IP任务</el-button>
    </template>
  </el-dialog>
</template>

<script>
import {ElMessage} from "element-plus";
import {cpu} from '@element-plus/icons-vue';

export default {
  components: {cpu},
  data() {
    return {
      flushLoading: false,
      changeIpLoading: false,
      dialogVisible: false,
      inputVisible: false,
      inputValue: '',
      user: {},
      changeIpParams: {
        ociCfgId: '',
        instanceId: '',
        cidrList: []
      },
      ociCfgDetails: {},
    };
  },
  mounted() {
    this.user = JSON.parse(this.$route.query.row || '{}')
    this.changeIpParams.ociCfgId = this.user.id
    this.getOciCfgDetails()
  },
  methods: {
    goBack() {
      this.$router.back(); // 返回上一个页面
    },
    handleClose(tag) {
      const index = this.changeIpParams.cidrList.indexOf(tag);
      if (index !== -1) {
        this.changeIpParams.cidrList.splice(index, 1);
      }
    },
    handleStopChangeIp(ocId) {
      const confirmed = window.confirm('确定要停止更换IP任务吗?');
      if (confirmed) {
        this.stopChangeIp(ocId);
      } else {
        this.$message.info('已取消');
      }
    },
    stopChangeIp(ocId) {
      this.$axios.post('/oci/stopChangeIp', {instanceId: ocId})
          .then(response => {
            if (response.data.success) {
              ElMessage({
                message: response.data.msg,
                type: 'success',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            }
            this.getOciCfgDetails()
          })
          .catch(error => {
            console.error('Error:', error)
          })
    },
    showInput() {
      this.inputVisible = true;
      this.$nextTick(() => {
        this.$refs.InputRef.focus();
      });
    },
    handleInputConfirm() {
      if (this.inputValue && !this.changeIpParams.cidrList.includes(this.inputValue)) {
        this.changeIpParams.cidrList.push(this.inputValue);
      }
      this.inputValue = '';
      this.inputVisible = false;
    },
    async getOciCfgDetails() {
      this.ociCfgDetails.instanceList = []
      this.flushLoading = true
      await this.$axios.post('/oci/details', {id: this.changeIpParams.ociCfgId})
          .then(response => {
            if (response.data.success) {
              this.ociCfgDetails = response.data.data
              ElMessage({
                message: response.data.msg,
                type: 'success',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
              this.flushLoading = false
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            }
          })
          .catch(error => {
            console.error('Error:', error)
          })
    },
    handleChangeIp(id) {
      this.dialogVisible = true
      this.changeIpParams.instanceId = id
    },
    changeIp() {
      this.changeIpLoading = true
      this.$axios.post('/oci/changeIp', {...this.changeIpParams})
          .then(response => {
            if (response.data.success) {
              ElMessage({
                message: response.data.msg,
                type: 'success',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
              this.changeIpLoading = false
              this.dialogVisible = false
              this.getOciCfgDetails()
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            }
          })
          .catch(error => {
            console.error('Error:', error)
          })
    }
  },

};
</script>

<style>
.instance-list {
  display: flex;
  flex-wrap: wrap;
  text-align: right;
}

.instance-card {
  border: 1px solid #ddd;
  padding: 16px;
  margin: 8px;
  width: 100%;
  max-width: 300px;
  border-radius: 8px;
  background-color: #f9f9f9;
  position: relative;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  text-align: left; /* Adjust text alignment */
}

.header {
  display: flex;
  align-items: center;
}

.server-icon {
  font-size: 24px;
  color: #409eff;
  margin-right: 10px; /* Add margin to separate the icon and text */
}

.ip-item {
  display: flex;
  align-items: center;
  margin: 4px 0;
}

button {
  margin-left: 8px;
  padding: 4px 8px;
  cursor: pointer;
}

.el-divider--horizontal {
  border-top: 1px var(--el-border-color) var(--el-border-style);
  display: block;
  height: 1px;
  margin: 10px 0;
  width: 100%;
}

</style>

